.footer {
    padding: 4rem 6rem;
    background: #000;
    color: #fff;
  }
  
  .top {
    display: flex;
    align-items: center;
    text-align: start;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .top i {
    color: #fff;
    font-size: 2rem;
    margin-left: 1rem;
  }
  
  .top i:hover {
    color: #01959a;
  }
  
  .bottom {
    padding: 2rem;
    text-align: start;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  
  .bottom div {
    display: flex;
    flex-direction: column;
  }
  
  .bottom h4 {
    font-size: 1.3rem;
    padding: 1rem 0 0.8rem 0;
  }
  
  .bottom a {
    text-decoration: none;
    color: #fafafa;
    padding-bottom: 0.4rem;
  }
  